@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* {
  font-size: 13.3px;
  background: black;
  color: white;
  font-family: 'Press Start 2P', cursive;
}

body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

button {
  background: white;
  color: black;
  margin: 3px;
  padding: 10px;
  border-radius: 10px;
}

#link {
	padding: 10px;
	border-radius: 10px;
	text-decoration: none;
}

.header-false {
  display: grid;
  grid-template-columns: repeat(1, 100%);
  align-items: center;
  background: white;
  height: 10%;
	width: 40px;
	margin-left: 5px;
  border-radius: 10px;
  transition: cubic-bezier(0.71, -0.1, 0.41, 1.21) 0.6s;
}

.header-true {
  display: grid;
  grid-template-columns: repeat(5, 20%);
  justify-items: end;
  align-items: center;
  background: white;
  height: 10%;
  width: 100%;
  border-radius: 10px;
  transition: cubic-bezier(0.71, -0.1, 0.41, 1.21) 0.6s;
}

.header-logo {
  border-radius: 10px;
  place-self: start;
  background: white;
  margin: 2px;
  color: black;
  height: 35px;
  width: 35px;
}

.header-exit-true {
  grid-column: 5/6;
  margin-right: 10px;
  opacity: 1;
  transition: ease-in-out 0.7s;
  background: white;
  color: black;
  place-self: center;
  color: white;
  background: red;
  border-radius: 10px;
  padding: 10px;
}

.header-exit-false {
  visibility: hidden;
  opacity: 0;
  background: white;
  color: black;
  place-self: center;
}

.header-room-true {
  border-radius: 10px;
  padding: 10px;
  border: 1px solid black;
  margin-right: 10px;
  grid-column: 2/5;
  opacity: 1;
  transition: ease-in-out 0.7s;
  background: white;
  color: black;
  align-self: center;
}

.header-room-false {
  visibility: hidden;
  opacity: 0;
  background: white;
  color: black;
  place-self: center;
}

.modal-dimmer {
  position: absolute;
  z-index: 10;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.753);
}

.modal-container {
  text-align: center;
  background: black;
  border: 2px solid white;
  padding: 10px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 180px;
  width: 250px;
  transform: translate(-50%, -50%);
}

.modal-content {
  margin-top: 30px;
}

.modal-actions {
  position: flex;
  margin-top: 60px;
}

.modal-button-enter {
  bottom: 10%;
	background: green;
	color: white;
  padding: 10px;
	border-radius: 10px;
	text-decoration: none;
}


.modal-button-leave {
  bottom: 10%;
	background: red;
	color: white;
  padding: 10px;
	border-radius: 10px;
	text-decoration: none;
}

.modal-button-cancel {
  bottom: 10%;
  background: white;
  color: black;
  padding: 10px;
	border-radius: 10px;
	text-decoration: none;
}

.lobby-container {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  width: 90%;
  height: 80%;
  margin-top: 20px;
  display: grid;
  grid-template-rows: 70% 30%;
}

.tab-content-info {
  border: 2px solid white;
  width: 95%;
  border-radius: 10px;
	z-index: 10;
	text-align: center;
}

.info-container {
  height: 100%;
  display: grid;
  grid-template-rows: 85% 15%;
  justify-items: center;
}

.lobby-players {
  margin: 10px 0px 10px;
}

.lobby-players-content {
  height: 100%;
  display: grid;
  grid-template-rows: 50% 50%;
  grid-template-columns: repeat(3, 33%);
  grid-gap: 5px;
}

#player-tag {
	max-width: 90%;
  text-align: center;
  border-radius: 10px;
  padding: 20px;
}

.submit-button {
  position: relative;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%);
}

.player-tag-inGame {
  background: gray;
  opacity: 0.5;
}

.player-tag-ready {
  opacity: 1;
}

.player-tag-notready {
  opacity: 0.5;
}

