.ReactSwipeButton {
  margin: 5px;
  float: left;
  width: 70%;
  left: 50%;
  transform: translateX(-50%);
  height: 50px;
  position: relative;
}
.ReactSwipeButton,
.ReactSwipeButton * {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  not supported by any browser */
}
.rsbContainer {
  border: 1px solid white;
  float: left;
  width: 100%;
  height: 100%;
  background: #eee;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.rsbContainerUnlocked {
  width: 50% !important;
  margin-left: 25%;
  transition: 0.5s;
  cursor: default;
}
.rsbContainerUnlocked .rsbcSlider {
  left: 100% !important;
  cursor: default;
  pointer-events: none;
}
.rsbContainerUnlocked .rsbcSliderArrow {
  transition: 0.5s;
  margin-right: -60px;
}
.rsbContainerUnlocked .rsbcSliderCircle {
  transition: 0.5s;
  margin-right: -60px;
}
.rsbcSlider {
  float: left;
  width: 100%;
  position: absolute;
  height: 50px;
  top: 0;
  left: 50px;
  margin-left: -100%;
  background: green;
  border-radius: 10px;
  z-index: 100;
}
.rsbcSliderText {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  line-height: 50px;
  text-align: center;
  letter-spacing: 2px;
  color: #fff;
  background: green;
  border-radius: 10px;
}
.rsbcSliderArrow {
  float: left;
  position: absolute;
  transform: rotate(45deg);
  border: 2px solid #fff;
  height: 8px;
  width: 8px;
  top: 50%;
  right: 22px;
  margin-top: -6px;
  border-left-color: transparent;
  border-bottom-color: transparent;
  transform-origin: center;
  background: green;
  z-index: 10;
}
.rsbcSliderCircle {
  position: absolute;
  right: 0;
  background: green;
  top: 0;
  height: 50px;
  width: 50px;
  border-radius: 10px;
}
.rsbcText {
  float: left;
  position: absolute;
  top: 0;
  left: 10%;
  right: 0;
  height: 50px;
  line-height: 50px;
  text-align: center;
  letter-spacing: 2px;
  font-size: 13px;
}

.active-tab {
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  background: black;
  color: white;
  border: 2px solid white;
  outline: 0;
}

.tab {
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  background: white;
  color: black;
  border: 2px solid white;
  outline: 0;
}

.buttongroup {
  border-radius: 5px;
	display: flex;
	justify-content: center;
}

.tab-content {
  margin-top: 5px;
  text-align: center;
  margin: 10px;
}


.gameplay-container {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
  width: 90%;
  height: 90%;
  display: grid;
  grid-template-rows: 66% 34%;
  grid-template-columns: 80% 20%;
}

.gameplay-players-container {
	transform: scale(90%);
  margin: 3px;
  grid-row: 1/2;
  grid-column: 2/3;
  display: grid;
  grid-template-rows: repeat(6, 15%);
  grid-gap: 5px;
}

.gameplay-history-container {
  margin: 5px 0px 0px 5px;
  grid-row: 1/2;
  grid-column: 1/2;
  border: 2px solid white;
  border-radius: 10px;
  transform: scale(1, -1);
  overflow: scroll;
}

.gameplay-code-container {
  grid-row: 2/3;
  grid-column: 1/3;
}

.history-container {
  display: flex;
  flex-direction: column;
  transform: scale(1, -1);
}

.history-unit {
  display: grid;
  grid-template-columns: 15% 75% 10%;
  margin: 2px 2px;
  padding: 10px;
  border: 1px solid white;
  border-radius: 10px;
}

.history-unit-round {
  border-radius: 10px;
  margin: auto;
  padding: 9px;
  background: white;
  color: black;
}

.history-unit-code {
  letter-spacing: 10px;
  font-size: 30px;
  justify-self: end;
}

.history-unit-result {
  display: grid;
  grid-gap: 1px;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
}

.gameplay-player-tag {
  padding: 15px;
  border-radius: 10px;
  margin: 0px 5px;
}

#note {
  padding: 12px;
  border-radius: 10px;
  margin: auto;
  border: 1px solid white;
}

.note-true {
  opacity: 1;
}

.note-false {
  transform: scale(0.8);
  opacity: 0.5;
}

.code {
  font-size: 20px;
  padding: 30px;
  border-radius: 10px;
  margin: auto;
  border: 1px solid white;
}

.login-container {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.installationPrompt {
  position: absolute;
  align-items: center;
  height: 35px;
  width: 35px;
  bottom: 20px;
  right: 20px;
  background: lightseagreen;
  padding: 10px;
  border-radius: 10px;
  font-size: 30px;
}

.landing-error {
	text-align: center;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: 20%;
	width: 60%;
	background: red;
	color: white;
	border-radius: 10px;
	padding: 10px;


}

.results-container {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  width: 90%;
  height: 90%;
  display: grid;
  margin-top: 20px;
  grid-template-rows: 55% 35% 10%;
}

.results-standings {
  grid-row: 1/2;
  border: 1px white solid;
  border-radius: 10px;
  overflow: scroll;
}

.podium-container {
}

.podium-unit {
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  margin: 5px;
}

.results-playerlist {
  grid-row: 2/3;
  border-radius: 10px;
  border: 1px solid white;
  overflow: scroll;
}

.results-actions {
  grid-row: 3/4;
  margin: auto;
}

.playercard-container {
  border: 1px white solid;
  border-radius: 10px;
  margin: 3px;
  padding: 5px;
  display: block;
  height: 100px;
  display: grid;
  grid-template-rows: repeat(4 25%);
}

.playercard-title {
  text-align: center;
  grid-row: 1/2;
}

.playercard-history {
  grid-row: 2/5;
}

